#homescreen-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding-top: 5vh;
    padding-bottom: 5vh;
    padding-left: 25vw;
    padding-right: 25vw;
    align-items: center;
}

#recording-display {
    width: 100%;
    height: 60vh;
    margin-bottom: 15px;
    overflow-y: scroll;
}

.paragraph{
    text-indent: 30px;
}

.button {
    background-color: #558fed;
    color: white;
    border-radius: 40px;
    text-align: center;
    width: 128px;
    margin-bottom: 15px;
    margin-left: 15px;
}

h5 {
    margin: 0;
}

#current-paragraph {
    margin: 15px;
}

.button-duo{
    display: flex;
}